import 'core-js/es/map';
import 'core-js/es/set';
import React from 'react';
import ReactDOM from 'react-dom';
import connect from '@vkontakte/vkui-connect';
import App from './App';
import mVKMiniAppsScrollHelper from '@vkontakte/mvk-mini-apps-scroll-helper';
import Persik from './panels/Persik';

import registerServiceWorker from './sw';



connect.subscribe((e) => {
    switch (e.detail.type) {
        case 'VKWebAppUpdateConfig':



                console.log("change scheme")
                let schemeAttribute = document.createAttribute('scheme');
                schemeAttribute.value = e.detail.data.scheme ? e.detail.data.scheme : 'client_light';
            document.body.attributes.setNamedItem(schemeAttribute);
            schemeAttribute= document.createAttribute('default_theme');
            schemeAttribute.value = e.detail.data.scheme ? e.detail.data.scheme : 'client_light';
            document.body.attributes.setNamedItem(schemeAttribute);
            schemeAttribute=document.createAttribute('schemecolor');

          //  schemeAttribute.value = e.detail.data.scheme === "client_light" ? "white" : '#19191a';
           // schemeAttribute.value = e.detail.data.scheme === "bright_light" ? "white" : '#19191a';

            if(e.detail.data.scheme === "client_light" || e.detail.data.scheme === "bright_light"){
                schemeAttribute.value="white"
            }else{
                schemeAttribute.value="#19191a"
            }

            document.body.attributes.setNamedItem(schemeAttribute);


            break;

        default:
            console.log(e.detail.type);
    }
});





// Init VK App

connect.send('VKWebAppInit', {});

// Если вы хотите, чтобы ваше веб-приложение работало в оффлайне и загружалось быстрее,
// расскомментируйте строку с registerServiceWorker();
// Но не забывайте, что на данный момент у технологии есть достаточно подводных камней
// Подробнее про сервис воркеры можно почитать тут — https://vk.cc/8MHpmT
registerServiceWorker();

const root = document.getElementById('root');

// Вызываем хелпер, передавая в аргумент корневой элемент приложения
mVKMiniAppsScrollHelper(root);

// Рендер приложения
ReactDOM.render(<App />, root);
